import { Lightbulb, TrendingUp, AlertTriangle } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';

export default function InsightsPanel({ insights, title = " Insights Inteligentes da Operao" }) {
  if (!insights || insights.length === 0) {
    return null;
  }

  return (
    <Card className="bg-gradient-to-br from-indigo-50 to-blue-50 border-indigo-200">
      <CardHeader>
        <CardTitle className="text-lg flex items-center gap-2">
          <Lightbulb className="w-5 h-5 text-indigo-600" />
          {title}
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {insights.map((insight, idx) => (
          <InsightItem key={idx} insight={insight} />
        ))}
      </CardContent>
    </Card>
  );
}

function InsightItem({ insight }) {
  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border border-indigo-100 hover:shadow-md transition-shadow">
      <div className="flex items-start gap-3">
        <div className="flex-shrink-0 w-8 h-8 bg-gradient-to-br from-indigo-500 to-blue-500 rounded-lg flex items-center justify-center">
          <TrendingUp className="w-5 h-5 text-white" />
        </div>
        <div className="flex-1 space-y-2">
          <h4 className="font-semibold text-slate-900">{insight.titulo}</h4>
          <p className="text-sm text-slate-600 leading-relaxed">{insight.texto}</p>
          {insight.acao && (
            <div className="flex items-start gap-2 mt-3 p-3 bg-indigo-50 rounded-lg border border-indigo-100">
              <AlertTriangle className="w-4 h-4 text-indigo-600 flex-shrink-0 mt-0.5" />
              <p className="text-sm text-indigo-900 font-medium">
                <span className="font-bold">Ao recomendada:</span> {insight.acao}
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
