import { useState } from 'react';
import { useAuth } from '../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import { TruckIcon, BarChart3, TrendingUp, Shield, CheckCircle2 } from 'lucide-react';
import { Button } from '../components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '../components/ui/dialog';
import { Input } from '../components/ui/input';
import { Label } from '../components/ui/label';

export default function LandingPage() {
  const [showLogin, setShowLogin] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const { login, loading } = useAuth();
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError('');
    
    const result = await login(username, password);
    if (result.success) {
      navigate('/dashboard');
    } else {
      setError(result.error);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-50">
      {/* Header */}
      <header className="fixed top-0 w-full bg-white/80 backdrop-blur-md border-b border-slate-200 z-50">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 bg-gradient-to-br from-blue-600 to-indigo-600 rounded-lg flex items-center justify-center">
              <TruckIcon className="w-6 h-6 text-white" />
            </div>
            <span className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
              LogDash
            </span>
          </div>
          <Button 
            data-testid="header-login-btn"
            onClick={() => setShowLogin(true)}
            className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white px-6 py-2 rounded-lg font-medium shadow-lg shadow-blue-500/30 transition-all duration-200"
          >
            Fazer Login
          </Button>
        </div>
      </header>

      {/* Hero Section */}
      <section className="pt-32 pb-20 px-6">
        <div className="max-w-7xl mx-auto text-center">
          <div className="inline-block mb-6 px-4 py-2 bg-blue-100 rounded-full">
            <span className="text-blue-600 font-semibold text-sm">Dashboard Inteligente de Anlise Logstica</span>
          </div>
          <h1 className="text-5xl lg:text-7xl font-bold mb-6 leading-tight">
            <span className="bg-gradient-to-r from-slate-900 via-blue-900 to-indigo-900 bg-clip-text text-transparent">
              Gerencie suas
            </span>
            <br />
            <span className="bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
              Operaes Logsticas
            </span>
          </h1>
          <p className="text-xl text-slate-600 mb-10 max-w-2xl mx-auto leading-relaxed">
            Anlise completa de entregas, SLA por transportadora e regio, visualizao de dados em tempo real.
            Tome decises estratgicas com base em dados precisos.
          </p>
          <Button 
            data-testid="hero-cta-btn"
            onClick={() => setShowLogin(true)}
            size="lg"
            className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white px-10 py-6 text-lg rounded-xl font-semibold shadow-2xl shadow-blue-500/30 transition-all duration-200 hover:scale-105"
          >
            Acessar Dashboard
          </Button>
        </div>
      </section>

      {/* Features Section */}
      <section className="py-20 px-6 bg-white/50 backdrop-blur-sm">
        <div className="max-w-7xl mx-auto">
          <div className="text-center mb-16">
            <h2 className="text-4xl font-bold text-slate-900 mb-4">O que o sistema faz</h2>
            <p className="text-lg text-slate-600">Anlises completas para otimizar sua operao logstica</p>
          </div>
          <div className="grid md:grid-cols-3 gap-8">
            <FeatureCard
              icon={<BarChart3 className="w-8 h-8" />}
              title="Anlise de Performance"
              description="Visualize SLA por transportadora e regio com tabelas e grficos interativos. Identifique pontos de melhoria rapidamente."
            />
            <FeatureCard
              icon={<TrendingUp className="w-8 h-8" />}
              title="Mtricas em Tempo Real"
              description="Acompanhe pedidos atrasados vs no prazo, total de entregas, e participao de cada transportadora em sua operao."
            />
            <FeatureCard
              icon={<Shield className="w-8 h-8" />}
              title="Upload de Dados"
              description="Faa upload de planilhas Excel com dados de entregas e obtenha anlises automticas instantneas."
            />
          </div>
        </div>
      </section>

      {/* Benefits Section */}
      <section className="py-20 px-6">
        <div className="max-w-7xl mx-auto">
          <div className="bg-gradient-to-br from-blue-600 to-indigo-600 rounded-3xl p-12 text-white shadow-2xl">
            <div className="grid md:grid-cols-2 gap-12 items-center">
              <div>
                <h2 className="text-4xl font-bold mb-6">Principais Benefcios</h2>
                <ul className="space-y-4">
                  <BenefitItem text="8 tipos de anlises automticas" />
                  <BenefitItem text="Visualizao clara com tabelas e grficos" />
                  <BenefitItem text="Dashboard responsivo e moderno" />
                  <BenefitItem text="Interface intuitiva e fcil de usar" />
                  <BenefitItem text="Dados precisos para tomada de deciso" />
                </ul>
              </div>
              <div className="bg-white/10 backdrop-blur-md rounded-2xl p-8 border border-white/20">
                <h3 className="text-2xl font-bold mb-4">Anlises Includas:</h3>
                <ul className="space-y-2 text-sm">
                  <li> Total de pedidos por Regio e Ms</li>
                  <li> Total de pedidos por Ms</li>
                  <li> % pedidos por Transportadora e Ms</li>
                  <li> Participao % por Transportadora</li>
                  <li> Atrasados x No Prazo</li>
                  <li> SLA por Transportadora</li>
                  <li> SLA por Transportadora e Regio</li>
                  <li> SLA consolidado por Regio</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 px-6">
        <div className="max-w-4xl mx-auto text-center">
          <h2 className="text-4xl font-bold text-slate-900 mb-6">Pronto para comear?</h2>
          <p className="text-xl text-slate-600 mb-10">
            Acesse agora e tenha insights poderosos sobre sua operao logstica
          </p>
          <Button 
            data-testid="bottom-cta-btn"
            onClick={() => setShowLogin(true)}
            size="lg"
            className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white px-10 py-6 text-lg rounded-xl font-semibold shadow-2xl shadow-blue-500/30 transition-all duration-200 hover:scale-105"
          >
            Acessar Agora
          </Button>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-slate-900 text-white py-8 px-6">
        <div className="max-w-7xl mx-auto text-center">
          <p className="text-slate-400"> 2025 LogDash - Dashboard de Anlise Logstica</p>
        </div>
      </footer>

      {/* Login Dialog */}
      <Dialog open={showLogin} onOpenChange={setShowLogin}>
        <DialogContent className="sm:max-w-md" data-testid="login-dialog">
          <DialogHeader>
            <DialogTitle className="text-2xl font-bold text-center">Login</DialogTitle>
          </DialogHeader>
          <form onSubmit={handleLogin} className="space-y-6 mt-4">
            <div className="space-y-2">
              <Label htmlFor="username">Usurio</Label>
              <Input
                id="username"
                data-testid="login-username-input"
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Digite seu usurio"
                required
                className="w-full"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Senha</Label>
              <Input
                id="password"
                data-testid="login-password-input"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Digite sua senha"
                required
                className="w-full"
              />
            </div>
            {error && (
              <div className="bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded-lg text-sm">
                {error}
              </div>
            )}
            <Button
              data-testid="login-submit-btn"
              type="submit"
              disabled={loading}
              className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white py-3 rounded-lg font-semibold"
            >
              {loading ? 'Entrando...' : 'Entrar'}
            </Button>
            <p className="text-xs text-center text-slate-500 mt-4">
              Credenciais demo: batlog / 123 ou editorajuspodivm / 123
            </p>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}

function FeatureCard({ icon, title, description }) {
  return (
    <div className="bg-white rounded-2xl p-8 shadow-lg hover:shadow-2xl transition-all duration-300 border border-slate-100 hover:border-blue-200 group">
      <div className="w-16 h-16 bg-gradient-to-br from-blue-600 to-indigo-600 rounded-xl flex items-center justify-center mb-6 group-hover:scale-110 transition-transform duration-300 text-white">
        {icon}
      </div>
      <h3 className="text-xl font-bold text-slate-900 mb-3">{title}</h3>
      <p className="text-slate-600 leading-relaxed">{description}</p>
    </div>
  );
}

function BenefitItem({ text }) {
  return (
    <li className="flex items-center gap-3">
      <CheckCircle2 className="w-6 h-6 flex-shrink-0" />
      <span className="text-lg">{text}</span>
    </li>
  );
}
