function ChartCard({ title, data, type, xField = '', yField = '' }) {
  let chartOptions = {};
  let series = [];

  if (type === 'bar') {
    // Bar chart for monthly orders
    const filteredData = data.filter(d => d.mes !== 'Total Geral');
    chartOptions = {
      chart: { type: 'bar', toolbar: { show: false }, fontFamily: 'inherit' },
      plotOptions: {
        bar: {
          borderRadius: 8,
          distributed: true,
          horizontal: false,
        }
      },
      colors: ['#3b82f6', '#6366f1', '#8b5cf6', '#a855f7', '#d946ef'],
      dataLabels: { enabled: false },
      xaxis: {
        categories: filteredData.map(d => d[xField]),
        labels: { style: { colors: '#64748b', fontSize: '12px' } }
      },
      yaxis: {
        labels: { style: { colors: '#64748b' } }
      },
      legend: { show: false },
      grid: { borderColor: '#e2e8f0' }
    };
    series = [{
      name: 'Pedidos',
      data: filteredData.map(d => d[yField])
    }];
  } else if (type === 'donut') {
    // Donut chart for delayed vs on-time
    const totalAtrasado = data.reduce((sum, d) => sum + d.atrasado, 0);
    const totalNoPrazo = data.reduce((sum, d) => sum + d.no_prazo, 0);
    
    chartOptions = {
      chart: { type: 'donut', fontFamily: 'inherit' },
      labels: ['Atrasado', 'No Prazo'],
      colors: ['#ef4444', '#22c55e'],
      legend: {
        position: 'bottom',
        labels: { colors: '#64748b' }
      },
      dataLabels: {
        enabled: true,
        formatter: function(val) {
          return val.toFixed(1) + '%';
        }
      },
      plotOptions: {
        pie: {
          donut: {
            size: '65%',
            labels: {
              show: true,
              total: {
                show: true,
                label: 'Total',
                color: '#64748b'
              }
            }
          }
        }
      }
    };
    series = [totalAtrasado, totalNoPrazo];
  } else if (type === 'bar-horizontal') {
    // Horizontal bar for SLA
    chartOptions = {
      chart: { type: 'bar', toolbar: { show: false }, fontFamily: 'inherit' },
      plotOptions: {
        bar: {
          borderRadius: 6,
          horizontal: true,
          distributed: true,
          dataLabels: { position: 'top' }
        }
      },
      colors: ['#3b82f6', '#6366f1', '#8b5cf6', '#a855f7', '#d946ef', '#ec4899', '#f43f5e'],
      dataLabels: {
        enabled: true,
        formatter: function(val) {
          return val.toFixed(1) + '%';
        },
        style: { colors: ['#fff'] }
      },
      xaxis: {
        categories: data.map(d => d.transportadora || d.regiao),
        labels: { style: { colors: '#64748b' } },
        max: 100
      },
      yaxis: {
        labels: {
          style: { colors: '#64748b', fontSize: '11px' },
          maxWidth: 150
        }
      },
      legend: { show: false },
      grid: { borderColor: '#e2e8f0' }
    };
    series = [{
      name: 'SLA %',
      data: data.map(d => d.sla)
    }];
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg">{title}</CardTitle>
      </CardHeader>
      <CardContent>
        <Chart options={chartOptions} series={series} type={type === 'bar-horizontal' ? 'bar' : type} height={320} />
      </CardContent>
    </Card>
  );
}

function FinancialKPICard({ title, value, icon, gradient }) {
  return (
    <Card className="overflow-hidden border-0 shadow-lg hover:shadow-xl transition-all duration-300">
      <CardContent className="p-6">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-2">
              <div className={`w-10 h-10 bg-gradient-to-br ${gradient} rounded-lg flex items-center justify-center text-white`}>
                {icon}
              </div>
              <p className="text-sm font-medium text-slate-600">{title}</p>
            </div>
            <p className="text-3xl font-bold text-slate-900 mt-2">{value}</p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
